<template>
	<div class="container">
				<hr style="border: 2px solid #F0AD4E; border-radius: 10px;" >
				
				<div class="registerBody">
				<el-form style="width: 350px;" :rules="a" >
				<el-form-item>
					<el-input placeholder="用户名" v-model="registerfrom.username">
					</el-input>
				</el-form-item>
				
				<el-form-item>
					<el-input placeholder="手机号" v-model="registerfrom.phone" >
					</el-input>
				</el-form-item>
				
				<el-form-item>
					<el-input placeholder="密码" v-model="registerfrom.password" >
					</el-input>
				</el-form-item>
				
				<el-form-item>
					<el-input placeholder="确认密码" v-model="passwordtow">
					</el-input>
				</el-form-item>
				
				<el-button class="col" @click="register" type="danger">提交注册</el-button>
				</el-form>
			</div>
			
	</div>
</template>

<script>
	import {Ajax} from '../../assets/js/ajax.js'
	export default{
		name:'RegisterBody',
		data:function(){
			return{
				registerfrom:{
					username:'',
					phone:'',
					password:''
				},
				a:{
					phone:[
						/* {required:true,message:'123'} */
						]
				},
				passwordtow:''
			}
		},
		methods:{
			register:function(){
				//判断注册
			if(this.registerfrom.username==""||
			this.registerfrom.phone==""||this.registerfrom.password==""||
			this.passwordtow==""){
				alert("输入框不能为空")
				return
			}
			if(this.registerfrom.password!=this.passwordtow){
				alert("两次输入密码不一样！")
				return
			}
			let data ="username="+this.registerfrom.username
			+"&phone="+this.registerfrom.phone+"&password="+this.registerfrom.password
			alert("先不要注册")
			return
			Ajax("post","user/registerUser",data,(data)=>{
				if(data.success==true){
					this.$alert(data.message,"注册成功",{
					dangerouslyUseHTMLString: true
					});
					this.$router.push('/login')	
				}else{
					alert(data.message)
				}
			}) 
			}
		}
	}
</script>

<style>
	.registerBody{
		height: 350px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>
